<template>
    <div>
        <div class="searchDiv">
            <div class="searchRow">
                <div class="searchCol">
                    <div class="labelDiv">用户名</div>
                    <div class="inputDiv">
                        <el-input v-model="queryForm.name" placeholder="Name" size="mini" clearable></el-input>
                    </div>
                </div>
                <div class="searchCol">
                    <div class="labelDiv">用户组</div>
                    <div class="inputDiv">
                        <el-select v-model="queryForm.user_group" placeholder="Group" size="mini" clearable>
                            <el-option v-for="(n,i) in groupOptions" :key="'g'+i" :label="n.label" :value="n.value"></el-option>
                        </el-select>
                    </div>
                </div>
                <div class="searchCol">
                    <div class="labelDiv">角色</div>
                    <div class="inputDiv">
                        <el-select v-model="queryForm.user_group_role" placeholder="Role" size="mini" clearable>
                            <el-option v-for="(n,i) in roleOptions" :key="'r'+i" :label="n.label" :value="n.value"></el-option>
                        </el-select>
                    </div>
                </div>
                <div class="searchCol">
                    <div class="labelDiv">邮箱</div>
                    <div class="inputDiv">
                        <el-input v-model="queryForm.email" placeholder="Name" size="mini" clearable></el-input>
                    </div>
                </div>
                <div class="searchCol">
                    <div class="labelDiv">状态</div>
                    <div class="inputDiv">
                        <el-select v-model="queryForm.status" placeholder="Role" size="mini" clearable>
                            <el-option :label="'activated'" :value="'activated'"></el-option>
                            <el-option :label="'pending'" :value="'pending'"></el-option>
                        </el-select>
                    </div>
                </div>
                <div class="searchBtnWrap">
                    <!-- <el-button type="primary" size="mini" icon="el-icon-search" @click="search">{{$t('button.search')}}</el-button>
                    <el-button size="mini" icon="el-icon-refresh" @click="reset">{{$t('button.reset')}}</el-button> -->
                    <el-button type="primary" size="mini" icon="el-icon-plus" @click="toAdd">{{$t('button.add')}}</el-button>
                </div>
            </div>
        </div>

        <el-table :data="userRecords" stripe border style="width: 100%" size="mini" tooltip-effect="dark">
            <el-table-column label="序号" type="index" show-overflow-tooltip width="55" align="center"></el-table-column>
            <el-table-column prop="name" label="用户名" show-overflow-tooltip align="center"></el-table-column>
            <el-table-column prop="user_group" label="用户组" show-overflow-tooltip align="center"></el-table-column>
            <el-table-column prop="campus_card_id" label="校园卡" show-overflow-tooltip align="center"></el-table-column>
            <el-table-column prop="department" label="院系" show-overflow-tooltip align="center"></el-table-column>
            <el-table-column prop="email" label="邮箱" show-overflow-tooltip align="center"></el-table-column>
            <el-table-column prop="user_group_role" label="角色" show-overflow-tooltip align="center"></el-table-column>
            <el-table-column prop="status" label="状态" show-overflow-tooltip align="center">
                <template slot-scope="scope">
                    <el-switch v-model="scope.row.status" active-color="#13ce66" inactive-color="#ff4949" :active-value="'activated'" :inactive-value="'pending'" @change="statusChange(scope.row)">
                    </el-switch>
                </template>
            </el-table-column>
            <el-table-column fixed="right" label="Oper" width="140" align="center">
                <template slot-scope="scope">
                    <el-button @click="toEdit(scope.row)" type="primary" size="small" icon="el-icon-edit-outline" circle class="editBtn"></el-button>
                    <el-button @click="toDel(scope.row)" type="primary" size="small" icon="el-icon-delete" circle class="delBtn"></el-button>
                </template>
            </el-table-column>
        </el-table>
        <!-- <div class="paginationDiv">
            <el-pagination background @size-change="sizeChange" @current-change="currentChange" :current-page="table.current" :page-size="table.size" layout="total, sizes, prev, pager, next, jumper" :total="table.total">
            </el-pagination>
        </div> -->

        <el-dialog class="userForm" :title="dTit" width="560px" :close-on-click-modal="false" :close-on-press-escape="false" :visible.sync="dialogVisible">
            <el-form ref="userForm" :model="userForm" label-width="100px" size="mini" :rules="userRules">
                <el-form-item label="用户名" prop="name">
                    <div style="height: 28px; overflow: hidden;">
                        <el-input v-model="userForm.name" style="width: 370px;"></el-input>
                    </div>
                </el-form-item>
                <el-form-item label="密码">
                    <el-input v-model="userForm.password" show-password style="width: 370px;"></el-input>
                </el-form-item>
                <el-form-item label="用户组" prop="user_group_id">
                    <el-select v-model="userForm.user_group_id" style="width: 370px;">
                        <el-option v-for="(n,i) in userGroupList" :key="'g'+i" :label="n.name" :value="n.id"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="校园卡号" prop="campus_card_id">
                    <el-input style="width: 370px;" v-model="userForm.campus_card_id"></el-input>
                </el-form-item>
                <el-form-item label="院系" prop="department">
                    <el-input style="width: 370px;" v-model="userForm.department"></el-input>
                </el-form-item>
                <el-form-item label="邮箱" prop="email">
                    <el-input style="width: 370px;" v-model="userForm.email"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialogFooter">
                <el-button size="mini" type="primary" @click="toSave">{{$t('button.confirm')}}</el-button>
                <el-button size="mini" @click="dialogVisible = false">{{$t('button.cancel')}}</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
export default {
    name: "userList",
    data() {
        return {
            queryForm: {},
            table: {
                size: 20,
                current: 1,
                pages: 0,
                total: 0,
                records: [],
            },
            dTit: '',
            dialogVisible: false,
            statusOptions: [],
            userTypeOptions: [],
            roleOptions: this.consts.roleOptions,
            records: [],
            groupOptions: [
                {
                    label: 'admin',
                    value: 'admin',
                },
                {
                    label: 'teacher',
                    value: 'teacher',
                },
                {
                    label: 'student',
                    value: 'student',
                },
            ],
            roleOptions: [
                {
                    label: 'admin',
                    value: 'admin',
                },
                {
                    label: 'member',
                    value: 'member',
                },
            ],
            userGroupList: [],
            userForm: {},
            userRules: {
                "user_group_id": [{ required: true, message: '用户组必选', trigger: 'change' }],
                "name": [{ required: true, message: '用户名必填', trigger: 'blur' }],
                // "password": [{ required: true, message: '密码必填', trigger: 'blur' }],
                // "phone": [{ required: true, message: '手机号码必填', trigger: 'blur' }],
                // "IMAGE_W": [{ required: true, pattern: /^-?[1-9]\d*$/, message: 'Intger Required', trigger: 'blur' }],

            },
        };
    },
    computed: {
        userRecords: function () {
            if (this.queryForm.name || this.queryForm.user_group || this.queryForm.user_group_role || this.queryForm.status) {
                return this.records.filter(obj => (
                    (!this.queryForm.name || (this.queryForm.name && obj.name.indexOf(this.queryForm.name) != -1))
                    && (!this.queryForm.user_group || (this.queryForm.user_group && this.queryForm.user_group == obj.user_group))
                    && (!this.queryForm.user_group_role || (this.queryForm.user_group_role && this.queryForm.user_group_role == obj.user_group_role))
                    && (!this.queryForm.status || (this.queryForm.status && this.queryForm.status == obj.status))
                    && (!this.queryForm.email || (this.queryForm.email && obj.email.indexOf(this.queryForm.email) != -1))
                ))
            }
            return this.records;
        },
    },
    methods: {
        loadUserGroup: function () {
            let _this = this;
            _this.req('/user_group', {}, function (data) {
                if (data && data.success) {
                    _this.userGroupList = data.obj.records;
                } else {
                    _this.$alert(data.message, _this.$t('tip.tip'), {
                        confirmButtonText: _this.$t('button.close'),
                        type: 'warning'
                    }).then(() => {
                    }).catch(() => { });
                }
            }, 'get', false);
        },
        search: function () {
            this.table.current = 1;
            this.getData();
        },
        //重置
        reset: function () {
            this.queryForm = {};
            this.getData();
        },
        sizeChange: function (size) {
            this.table.size = size;
            this.getData();
        },
        currentChange: function (current) {
            this.table.current = current;
            this.getData();
        },
        getData: function () {
            let _this = this;
            _this.req('/user', {}, function (data) {
                if (data.success) {
                    _this.records = data.obj.records;
                } else {
                    _this.$alert(data.message, _this.$t('tip.tip'), {
                        confirmButtonText: _this.$t('button.close'),
                        type: 'warning'
                    }).then(() => {
                    }).catch(() => { });
                }
            }, 'get');
        },
        toDel: function (row) {
            let _this = this;
            this.$confirm(this.$t('tip.delTip'), this.$t('tip.tip'), {
                confirmButtonText: this.$t('button.confirm'),
                cancelButtonText: this.$t('button.cancel'),
                type: 'warning'
            }).then(() => {
                // 执行删除操作
                this.req('/user/' + row.id, {}, function (data) {
                    if (data.success) {
                        _this.$message({
                            message: _this.$t('tip.success'),
                            type: 'success'
                        });

                        _this.getData();
                    } else {
                        _this.$alert(data.message, _this.$t('tip.tip'), {
                            confirmButtonText: _this.$t('button.close'),
                            type: 'warning'
                        }).then(() => {
                        }).catch(() => { });
                    }
                }, "delete");

            }).catch(() => { });
        },
        toEdit: function (row) {
            this.dTit = 'Edit user';
            this.dialogVisible = true;
            this.userForm = JSON.parse(JSON.stringify(row));
        },
        toAdd: function () {
            this.dTit = 'Add user';
            this.dialogVisible = true;
            this.userForm = {};
        },
        toSave: function () {
            let _this = this;

            this.$refs['userForm'].validate(valid => {
                if (valid) {
                    if (!_this.userForm.id) {
                        _this.req('/user', _this.userForm, function (data) {
                            if (data && data.success) {
                                _this.dialogVisible = false;
                                _this.getData();
                            } else {
                                _this.$alert(data.message, _this.$t('tip.tip'), {
                                    confirmButtonText: _this.$t('button.close'),
                                    type: 'warning'
                                }).then(() => {
                                }).catch(() => { });
                            }
                        });
                    } else {
                        _this.req('/user/' + _this.userForm.id, _this.userForm, function (data) {
                            if (data && data.success) {
                                _this.dialogVisible = false;
                                _this.getData();
                            } else {
                                _this.$alert(data.message, _this.$t('tip.tip'), {
                                    confirmButtonText: _this.$t('button.close'),
                                    type: 'warning'
                                }).then(() => {
                                }).catch(() => { });
                            }
                        }, "put");
                    }
                } else {
                    _this.$message.error("校验失败，请认真填写");
                }
            });
        },
        statusChange: function (row) {
            let _this = this;
            delete row.email;
            _this.req('/user/' + row.id, row, function (data) {
                if (data && data.success) {
                    // 
                } else {
                    _this.$alert(data.message, _this.$t('tip.tip'), {
                        confirmButtonText: _this.$t('button.close'),
                        type: 'warning'
                    }).then(() => {
                    }).catch(() => { });
                }
            }, "put");
        },
    },
    mounted: function () {
        this.$nextTick(function () {
            this.loadUserGroup();
            this.getData();
        });
    }
};
</script>

<style scoped>
.userForm .el-input,
.userForm .el-select,
.userForm .el-cascader {
    width: 380px;
}
</style>



